#navbar {
    width: 100vw;
    background-color: #383838;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

#nav-title {
    color: #ddd;
    width: max-content;
    font-size: 24px;
    font-weight: 400;
    flex-grow: 0;
    padding: 0 2%;
}

#navbar ul {
    list-style: none;
    display: flex;
    align-content: center;
    color: #ddd;
    width: max-content;
    margin-right: auto;
    margin-bottom: 0;
    height: 100%;
}

#navbar ul li {
    text-decoration: none;
    list-style: none;
    color: #ddd;
    background-color: #383838;
    height: 40px;
    padding: 0 10px;
    display: flex;
    align-items: center;
}

#navbar ul li p, p {
    height: fit-content;
    margin: 0;
    padding: 0;
    color: #ddd;
}

#navbar ul .selected li, #navbar ul li:hover {
    background-color: #121212;
    color: white;
}

#navbar>p {
    color: #ddd;
    width: fit-content;
    margin-right: 5%;
}