.file-box {
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    width: 20%;
    padding: 15px;
    cursor: pointer;
}

.file-box a {
    color: #383838;
}

.file-box p {
    font-weight: 700;
    font-size: 18px;
    text-align: center;
    color: #383838;
}

.file-box i {
    font-size: 116px;
    color: #383838;
}